import React from 'react'
import { createElement } from 'react'
import Dashboard from './Dashboard'
const useCreateElement = createElement
const useDashboard = Dashboard
const useNamesForMinifier = function(a, b) {
    return [useCreateElement, useDashboard]
}

const Init = ({opfsRoot, setMaincontent}) => {
    const handleFileInputChange = async (event) => {
        const file = event.target.files[0];
        if (!file) return; // If no file selected, return early
    
        try {
          const fileReader = new FileReader()
          fileReader.onload = (e) => {
            eval(e.target.result)
          }
          fileReader.readAsText(file)
        } catch (error) {
          console.error('Error reading file:', error)
        }
      }
    return (
        <>
            <label htmlFor="Upload">Upload (if not cached):</label>
            <input id="Upload" type="file" onChange={handleFileInputChange} />
        </>
    )
}

export default Init